
// ===== catalog ===== 

@if 1 {
  .shop__top {
    position: relative;
    padding: 20px 0px 5% 0px;
    background-image: url(../img/bg.svg);
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: bottom left;
  }

  .shop__title {
    margin-bottom: 20px;
    text-align: center;
    color: #fff;
  }

  .shop__input {
    font-size: 14px;
  }

  .input__wrap {
    display: flex;
    margin-bottom: 40px;
  }

  .shop__btn-submit {
    flex-shrink: 0;
    width: 50px;
    height: 50px;
    margin-left: 10px;
  }

  .shop__btn-submit span {
    display: none;
  }

  @media (pointer: fine),
  (min-width:0\0) {
    .shop__input:hover {
      color: #2A2929;
      background-color: #fff;
    }
  }

  .shop__input:focus {
    color: #2A2929;
    background-color: #fff;
  }



  .filter {
    display: none;
    font-size: 14px;
  }

  .filter__row {
    margin-bottom: 20px;
  }

  .icon-down {
    display: none;
  }

  .shop-pin__btn.active .icon-down {
    display: block;
  }

  .shop-pin__btn.active .icon-equalizer {
    display: none;
  }





  .filter__title {
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 24px;
    color: $color-hover;
  }

  @media (min-width: 480px) {
    .filter {
      font-size: 16px;
    }

    .filter__row {
      margin-bottom: 0px;
    }

    .filter__title br {
      display: none;
    }
  }

  @media (min-width: 768px) {
    .shop__btn-submit {
      width: 166px;
      height: 60px;
    }

    .shop__btn-submit img {
      display: none;
    }

    .shop__btn-submit span {
      display: block;
    }

    .filter {
      font-size: 18px;
    }
  }

  @media (min-width: 1200px) {
    .shop {
      padding: 0px 0px 2% 0px;
    }

    .shop__btn-submit {
      width: 232px;
    }

    .filter {
      display: block;
    }

    .shop__pin {
      display: none;
    }
  }
}



// ===== category ===== 

@if 1 {
  .category {
    padding: 50px 0px 0px 0px;
  }

  .category__row {
    margin: 0 -5px;
    margin-bottom: 30px;
  }

  .category__col {
    padding: 0 5px;
    margin-bottom: 25px;
  }

  .category__item {
    display: flex;
    flex-direction: column;
    height: 100%;
    min-height: 125px;
    box-shadow: -12px -12px 24px #FFFFFF, 12px 12px 24px #E9E9EC;
    border-radius: 5px;
  }

  .category-item__top {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding: 0px 15px 10px 15px;
    background: linear-gradient(109.5deg, #EEEEF1 6.26%, #FFFFFF 93.74%);
  }

  @media (pointer: fine),
  (min-width:0\0) {
    .category__item:hover {
      box-shadow: 0px 12px 12px rgba(109, 113, 249, 0.25);
    }

    .category__item:hover .category-item__top {
      background-color: #fff;
      background-image: none;
    }

    .category__item:hover .category__link {
      background-color: #F5F5F8;
    }
  }


  .category__icon {
    display: flex;
    justify-content: center;
    margin-bottom: 15px;
  }

  .category__icon img {
    margin-top: -15px;
    margin-right: 10px;
  }

  .category__icon img:last-child {
    margin-right: 0px;
  }


  .category-item__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .category__title {
    margin-bottom: 11px;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    // text-align: center;
    text-transform: uppercase;
  }

  .category__rating {
    font-size: 14px;
  }

  @media (min-width: 480px) {
    .category__row {
      margin-bottom: 40px;
    }

    .category__item {
      min-height: 210px;
    }

    .category__rating {
      justify-content: center;
    }

    .category-item__row {
      flex-grow: 1;
      flex-direction: column;
      align-items: center;
    }
  }


  .rating-show {
    display: flex;
  }

  .rating-show span {
    color: #D3D4FD;
    line-height: 1;
    // letter-spacing: 10px;
  }

  .rating-show span.active {
    color: #6D71F9;
  }

  .rating-show>span:before {
    content: "★";
    display: block;
  }


  .category__img {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 15px;
  }

  .category__img img {
    max-width: 70px;
  }

  .category__link {
    flex-shrink: 0;
    display: block;
    padding: 6px 15px;
    color: #B8B9D0;
    text-align: center;
    font-size: 14px;
    line-height: 17px;
    background-color: #fff;
  }

  @media (min-width: 480px) {
    .category__img {
      flex-grow: 1;
    }

    .category__img img {
      max-width: 100%;
    }
  }


}



// ===== more =====

@if 1 {
  .more__btn {
    font-size: 14px;
    color: #B8B9D0;
    cursor: pointer;
  }

  .more__btn.hidden {
    display: none;
  }

  .more p:nth-of-type(n+2) {
    display: none;
  }

  @media (min-width: 768px) {
    .more p:nth-of-type(-n+2) {
      display: block;
    }
  }

  @media (min-width: 1200px) {
    .more p:nth-of-type(n+1) {
      display: block;
    }

    .more__btn {
      display: none;
    }
  }
}



