
// ==== select ==== 

@if 1 {

  .select-wrap {
    position: relative;
    margin-bottom: 20px;
    font-size: 14px;
  }

  .select-wrap:after,
  .select-custom {
    z-index: 3;
  }

  // arrow
  .select-wrap:after {
    content: "";
    position: absolute;
    top: 50%;
    right: 20px;
    display: block;
    border: 5px solid transparent;
    border-top-color: #fff;
    pointer-events: none;
  }

  .select-wrap.focus:after,
  .select-wrap.isActive:after,
  .select-wrap.oupen:after {
    border-top-color: $color-hover;
  }

  .select-wrap.isActive:after,
  .select-wrap.oupen:after {
    transform: rotate(180deg) translateY(5px);
  }

  .select,
  .select-custom {
    width: 100%;
    height: 50px;
  }

  .select-custom {
    position: absolute;
    top: 0;
    left: 0;
    display: none;
  }

  .select,
  .select-custom__btn {
    padding: 0px 20px 0px 30px;
    color: #fff;
    border: 2px solid transparent;
    border-radius: 30px;
  }

  .select {
    position: relative;
    display: block;
    background-color: rgba(#fff, .1);
    box-shadow: -12px -12px 24px #7D82FF, 12px 12px 24px #5D60D4;
    appearance: none;
  }

  .select-custom__btn {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: #7D82FF;
    cursor: pointer;
  }

  .select-custom__btn::before,
  .select-custom__option::before {
    margin-right: 10px;
  }

  .select:focus,
  .select-custom.isActive .select-custom__btn {
    color: #2A2929;
    background-color: #fff;
    border-color: $color-hover;
    transition: .2s ease 0s;
  }

  .select:invalid,
  .select-custom__btn.invalid {
    color: rgba(255, 255, 255, 0.5);
  }

  .select:focus:invalid,
  .select-custom.isActive .select-custom__btn.invalid {
    color: #B8B9D0;
  }

  .select option {
    color: #2A2929;
  }

  // ===== state =====

  @media (pointer: fine),
  (min-width:0\0) {
    .select-wrap:hover:after {
      border-top-color: $color-hover;
    }

    .select:hover,
    .select-wrap:hover .select-custom__btn {
      color: #2A2929;
      background-color: #fff;
      border: 2px solid #5CDEDA;
      transition: .2s ease 0s;
    }

    .select-wrap:hover .select:invalid,
    .select-wrap:hover .invalid {
      color: #B8B9D0;
    }

    .select-custom {
      display: block;
    }

    .select:focus+.select-custom {
      display: none;
    }
  }

  @media (min-width: 480px) {
    .select-wrap {
      font-size: 16px;
    }
  }

  @media (min-width: 768px) {
    .select-wrap {
      font-size: 18px;
    }

    .select,
    .select-custom {
      height: 60px;
    }
  }


  // === select-custom options ===

  .select-custom__options {
    position: absolute;
    top: 80px;
    left: 0;
    display: none;
    width: 100%;
    padding: 20px 0;
    line-height: 28px;
    border: 1px solid #6f6f6f;
    border-radius: 5px;
    background-color: #fff;
    box-shadow: 0 0 4px #e9e1f8;
  }

  .select-custom.isActive .select-custom__options {
    display: block;
  }

  // === select-custom option ===

  .select-custom__option {
    position: relative;
    padding: 5px 5px 5px 30px;
    cursor: pointer;
  }

  .select-custom__option.isHover {
    background-color: #5CDEDA;
    /*contrast AA */
    color: #fff;
  }

  .select-custom__option:not(:last-of-type)::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    border-bottom: 1px solid #d3d3d3;
  }

  .select-custom__option.isActive::before {
    // content: "✓";
    // position: absolute;
    // left: 5px;
  }

}



