    }

    .select2-custom {
      display: block;
    }

    .select2:focus+.select2-custom {
      display: none;
    }
  }


  @media (min-width: 480px) {
    .select2-wrap {
      font-size: 16px;
    }
  }

  @media (min-width: 768px) {
    .select2-wrap {
      font-size: 18px;
    }

    .select2,
    .select2-custom {
      height: 60px;
    }
  }


  // === select2-custom optionS ===

  .select2-custom__options {
    position: absolute;
    z-index: 2;
    top: 80px;
    left: 0;
    display: none;
    width: 100%;
    padding: 20px 0;
    line-height: 28px;
    // border: 1px solid #6f6f6f;
    border-radius: 5px;
    background-color: #fff;
  }

  .select2-custom.isActive .select2-custom__options {
    display: block;
  }


  // === select2-custom option ===

  .select2-custom__option {
    position: relative;
    padding: 5px 5px 5px 30px;
    cursor: pointer;
  }

  // залипает как native
  .select2-custom__option.isHover {
    // background-color: #5CDEDA;
    /*contrast AA */
    // color: #fff;
  }

  .select2-custom__option.isActive {
    background-color: $color-hover;
    color: #fff;
  }

  .select-custom__option.isActive::before {
    // content: "✓";
    // position: absolute;
    // left: 5px;
  }

  .select2-custom__option:not(:last-of-type)::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    // border-bottom: 1px solid #d3d3d3;
  }



  @media (min-width: 1200px) {
    .select2-wrap:after {
      display: none;
    }

    .select2,
    .select2-custom__btn {
      position: absolute;
      top: 0px;
      left: 0px;
    }

    .select2-custom__btn {
      opacity: 0;
    }

    .select2-custom {
      position: relative;
      height: auto;
    }

    .select2-custom__options {
      position: static;
      display: block;
      padding: 0;
      background-color: #F5F5F8;
    }

    .select2-custom__option {
      padding: 15px 5px 15px 30px;
      border-radius: 30px;
    }

    .select2-custom__option.isActive {
      background-image: linear-gradient(104.98deg, #6266E0 0%, #7579FF 100%);
      box-shadow: -12px -12px 24px #FFFFFF, 12px 12px 24px #E9E9EC;
    }

    .select2-custom__option.isActive::before {
      color: #fff;
    }


    @media (pointer: fine),
    (min-width:0\0) {
      .select2-custom__option:not(.isActive):hover {
        color: $color-hover;
        background-color: transparent;
      }
    }

    .select2-custom__option:not(.isActive):active {
      color: $color-active;
    }
  }
}