// ==== General ==== 

body {
	// min-width: 320px;
	overflow-x: hidden;
	font-family: 'Rubik', sans-serif;
	font-size: 14px;
	// font-weight: 300;
	line-height: 1.55;
	color: #2A2929;
}

body.lock {
	overflow: hidden;
}

@media (min-width: 480px) {
	body {
		font-size: 16px;
	}
}

@media (min-width: 768px) {
	body {
		font-size: 18px;
	}
}

.wrapper {
	padding-top: 60px;
	display: flex;
	flex-direction: column;
	width: 100%;
	min-height: 100%;
	background-color: #F5F5F8;
	// overflow: hidden;
}

@media (min-width: 768px) {
	.wrapper {
		padding-top: 80px;
	}
}

main {
	flex: 1 0 auto;
}

.footer {
	flex: 0 0 auto;
}

.img_cover {
	position: absolute;
	height: 100%;
	width: 100%;
	object-fit: cover;
	font-family: 'object-fit: cover;';
}

._cover {
	position: relative;
}

._cover img {
	position: absolute;
	height: 100%;
	width: 100%;
	object-fit: cover;
	font-family: 'object-fit: cover;';
}

.visuallyhidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	border: 0;
	overflow: hidden;
	clip: rect(0 0 0 0);
}

.hidden {
	display: none !important;
}

*:last-child {
	margin-bottom: 0;
}


p {
	// margin-bottom: 31px;
}

.mb_30 {
	margin-bottom: 30px;
}

.mr-10 {
	margin-right: 10px;
}


.box-icon {
	display: flex;
	width: 100%;
}

.box-icon img:not(:last-child) {
	margin-right: 10px;
}

.round {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 50px;
	height: 50px;
	margin-left: auto;
	border-radius: 50%;
	background-color: $color-hover;
}