// ===== cabinet ===== 

.user {}

.user__link {
  padding: 0 20px;

  @media (min-width: 768px) {
    display: none;
  }
}

// icon arrow

.user__link::before {
  position: absolute;
  right: 15px;
  top: 19px;
  color: $color-primary;
}




// ===== tabs ===== 

@if 1 {
  .tabs {
    display: none;

    @media (min-width: 768px) {
      display: block;
    }
  }

  .tabs__list {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
    background: linear-gradient(109.5deg, #EEEEF1 6.26%, #FFFFFF 93.74%);
    box-shadow: -12px -12px 24px #FFFFFF, 12px 12px 24px #E9E9EC;
    border-radius: 30px;
  }

  .tabs__btn {
    flex: 1 0 0;
    position: relative;
    height: 60px;
    border-radius: 30px;
  }
  @media (pointer: fine), (min-width:0\0) {
    .tabs__btn:hover {
      background-color: #fff;
    }
  }


  .tabs__panel {
    display: none;
  }

  .tabs__panel.active {
    display: block;
  }
}



// ===== spoiler ===== 

@if 1 {
  .spoiler-user__btn {
    padding: 0 30px;
  }

  .spoiler-user__panel {
    padding-top: 40px;
  }

}


