// ===== title =====

.title_1,
.title_2,
.title_3 {
  font-weight: 500;
}

.title_1 {
  color: #fff;
  font-size: 70px;
  line-height: (80/70);
}

.title_2 {
  font-size: 50px;
  line-height: (59/50);
}

.title_3 {
  font-size: 24px;
  line-height: 1.2;
  margin-bottom: 15px;
}

@media (max-width: 1200px) {
  .title_1 {
    font-size: calc(30px + (70 - 30) * ((100vw - 320px) / (1200 - 320)));
  }

  .title_2 {
    font-size: calc(26px + (50 - 26) * ((100vw - 320px) / (1200 - 320)));
  }

  .title_3 {
    font-size: calc(18px + (24 - 18) * ((100vw - 320px) / (1200 - 320)));
  }
}

.subtitle {
  font-weight: 600;
  color: #4976f6;
  margin-bottom: 9px;
}